import React, { useState } from "react"
import "./Billing.sass"
import { reduxForm } from "redux-form"
import {
  FormControlLabel,
  Checkbox,
  Button,
  Typography,
  Box,
  Card
} from "@material-ui/core"
import { validate } from "validation"
import PaymentFields from "./PaymentFields/PaymentFields"
import FormsPreview from "./../FormsPreview/FormsPreview"
import CheckoutForm from "./../CheckoutForm/CheckoutForm"
import { notify } from "components/Toaster/Toaster"


const Billing = ({
  handleSubmit,
  invalid,
  submitting,
  pristine,
  onSubmit,
  shipping,
  step,
 }) => {
  const {firstname, lastname, address, city, zip, country} = shipping
  const previewContent = { fullname: `${firstname} ${lastname}`, address, city, zip, country }
  const [checked, setChecked] = useState(true)
  const submit = async ({cardnumber, exp, cvv, ...rest}) => {
    const {phone, email, ...shippingRest} = shipping
    const data = {
      addresses: {
        shipping,
        billing: {
          card: {
            cardnumber,
            exp,
            cvv,
          },
          address: checked ? {...shippingRest} : {...rest}
        },
      }
    }

    await onSubmit(data, (type, msg) => notify(type, msg))
  }

  return (
    <Card className="billing">
      <div className="billing-title">
        <Typography variant="h5" component="h4" children="PAYMENT" />
      </div>
      {step >= 2 && <form onSubmit={handleSubmit(submit)}>
          <PaymentFields />

          <FormControlLabel
            className="billingAddressCheckbox"
            control={<Checkbox checked={checked} onChange={() => setChecked(!checked)} />}
            label="Billing address same as shipping"
          />

          {checked ?
            <FormsPreview
              title="Shipping Address"
              content={previewContent}
              cn="billingAddress"
            /> :
            <CheckoutForm type="billing" />
          }

          <Box alignSelf="flex-end" mt={3}>
            <Button
              size="large"
              variant="contained"
              color="secondary"
              type="submit"
              disabled={invalid || submitting || pristine}
            >
              PLACE ORDER
            </Button>
          </Box>
        </form>}
    </Card>
  )
}

export default reduxForm({
  form: "billing",
  validate,
  enableReinitialize: true
})(Billing)
